.fhead{
    position: fixed;
    top: 12vh;
    overflow: hidden;
}
.fhead>ul{
    overflow: hidden;
}
.fhead>ul>li{
    list-style: none;
}

nav#focusTools[fc='unfocus']{
    width: 100%;
    height: 0;
}
nav#focusIndex[fc='unfocus']{
    width: 0;
    height: 100%;
}

/*
****INDEX
*/

nav#focusIndex{
    width: 40vh;
    height: 100%;
    background-color: rgb(214, 46, 46);
    z-index: 2;
    flex-direction: column;
    justify-content: center;
}
nav#focusIndex>div{
    height: 1em;
}

nav#focusIndex>ul{
    display: flex;
    width: 100%;
    height: auto;

    flex-direction: column;
    align-items: center;
}
nav#focusIndex>ul>li{
    position: relative;
    display: flex;
    width: 100%;
    height: 8vh;
    margin: .4vh;
    
    box-shadow: rgba(111, 21, 21, 0.2) 1px 1px 6px 2px;
}
nav#focusIndex>ul>li>a{
    position: relative;
    margin: auto;
    font-size: 3vh;
    justify-items: center;
}

/*
****Tools
*/

nav#focusTools{
    display: flex;
    width: 100%;
    height: 40vh;
    background-color: rgb(87, 33, 137);
}

nav#focusTools>ul{
    padding: 1vh;
    position: relative;
    margin: .8em;
    outline: dashed black;
    flex-grow: 1;
}