#principal{
    top: 0;
    position: fixed;
    width: 100%;
    height: 12vh;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    z-index: 10;
}

#principal>nav{
    position: relative;
    margin-left: .4em;
    margin-right: .4em;
    min-width: 8vh;
}

#principal>nav#index{
    height: 98%;
    width: 20%;
}

#principal>nav#search{
    margin-top: 2vh;
    height: 80%;
    width: 20%;
    flex-grow: 2;
}

#principal>nav#search>input{
    position: relative;
    padding: .2vh;
    margin: .8vh;
    width: 100%;
    height: 6vh;
}

#principal>nav#tools{
    position: relative;
    height: 98%;
    flex-grow: 1.2;
}

#principal button{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    cursor: alias;
    border-color: transparent;
    z-index: 2;
    background-color: transparent;
}

#principal img{
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: fill;
}

#principal>nav#index img{
    width: 14vh;
    height: 12vh;
    left: -.4em;
}

#principal>nav#tools img{
    width: 14vh;
    height: 10vh;
    margin: auto;
}

/*pc*/
@media only screen and (min-width: 667px){
    #principal>nav{
        margin-left: 1.5em;
        margin-right: 1.5em;
    }

    #principal>nav#index img{
        width: 18vh !important;
        height: 14vh;
    }

    #principal>nav#tools img{
        margin: auto;
        width: 16vh;
        height: 12vh;
        right: 0;
    }
}